<template>
    <div>
        <!-- MENSAGEM ENVIADA -->
        <div v-if="c.tipo == 'SENT'">
            <hr> Recebido: {{ c.mt }}
        </div>

        <!-- MENSAGEM RECEBIDA -->
        <div v-if="c.tipo == 'MSG'">
            <hr> Enviado: {{ mt }}
        </div>

        <!-- FOTO RECEBIDA -->
        <div v-if="c.tipo == 'FOT'">
            <hr> Enviado:
            <img :src="c.foto" height="200">

        </div>

        <!-- MENU RECEBIDO -->
        <div v-if="c.tipo == 'MENU'">
            <div v-for="botao in c.botoes">
                <button @click="envia(botao)">{{ botao }}</button>
            </div>
        </div>
    </div>
</template>

<style scoped>
</style>

<script>
    import bus from '../lib/bus'

    export default {
        props: ['c'],
        data() {
            return {
                mt: null
            }
        },
        mounted() {
            if (this.c.tipo == 'MSG') {
                const mt = this.c.mt
                    .replace('@nome@', localStorage.getItem('nome'))
                    .replace('@idade@', localStorage.getItem('idade'))

                this.divMSG(mt)
                return
            }

            if (this.c.tipo == 'FOT') {
                setTimeout(() => {
                    bus.$emit('callProximo', 1)
                }, 1500)
                return
            }
        },
        methods: {
            envia(msg) {
                bus.$emit('envia', msg)
            },
            divMSG(mt) {
                this.mt = ''
                let interval = 10
                let proximoInterval
                let pos = 0
                let len = mt.length
                let ultimoFoiPontuacao = false

                // const velocidadeMIN = 15
                // const velocidadeMAX = 40

                const velocidadeMIN = 1
                const velocidadeMAX = 4

                const proximaLetra = () => {
                    setTimeout(() => {
                        this.mt += mt[pos++]
                        interval = proximoInterval
                        if (mt[pos] && ['.', ',', '!', '?'].indexOf(mt[pos]) > -1 && !ultimoFoiPontuacao) {
                            proximoInterval = Math.floor(Math.random() * (velocidadeMAX * 10 - velocidadeMIN * 10 + 1)) + velocidadeMIN * 10
                            ultimoFoiPontuacao = true
                        } else {
                            proximoInterval = Math.floor(Math.random() * (velocidadeMAX - velocidadeMIN + 1)) + velocidadeMIN
                            ultimoFoiPontuacao = (mt[pos] && ['.', ',', '!', '?'].indexOf(mt[pos]) > -1)
                        }

                        if (!mt[pos]) {
                            setTimeout(() => {
                                bus.$emit('callProximo', 1)
                            }, 300)
                            return
                        }

                        proximaLetra()
                    }, interval)
                }

                proximaLetra()
            },
        }
    }
</script>


