const menu1 = {
    tipo: 'MENU',
    botoes: ['Uma Pausa Rapida', 'Continua!'],
    "Uma Pausa Rapida": 'fluxoDepois'
}

const menuContinuar = {
    tipo: 'MENU',
    botoes: ['Continuar de onde parou.', 'Começar do início!'],
    "Continuar de onde parou.": 'start', //Inicia o fluxo "start" de onde parou
    "Começar do início!": 'start',
    "Começar do início!|passo": 0   //Inicia o fluxo "start" desde o inicio
}

const quickbuttons1 = {
    tipo: 'QUICKBUTTONS',
    botoes: ['nadar', 'dormir', 'correr', 'praia', 'mais nada'],
    componente: 'ferias'
}

module.exports = {
    start: [
        { tipo: 'MSG', mt: `Olá` },
        
        { tipo: 'MSG', mt: `Essa é a versão Beta do On the GO, clique no botão para responder sua pesquisa.` },
        { tipo: 'MSG', mt: `Qual é o seu nome?` },
        { tipo: 'PER', placeholder: 'Digite seu nome.', componente: 'nome' },
        { tipo: 'MSG', mt: `Qual sua idade?`, componente: 'idade'},
        { tipo: 'PER', placeholder: 'Digite sua idade', filtro: 'numero', componente: 'idade' },
        { tipo: 'MSG', mt: `Legal @nome@, entendi que você tem @idade@ anos.` },
        { tipo: 'MSG', mt: `ta tomando forma... Agora ta ficando legal! :-) Uhull! Gostei! Show, agora sim! ta tomando forma... Agora ta ficando legal! :-) Uhull! Gostei! ` },
        { tipo: 'MSG', mt: `Queres dar uma pausa rápida? ou queres continuar adiante?` },
        menu1,
        { tipo: 'MSG', mt: `Legal. Vou te mandar uma foto agora:` },
        { tipo: 'FOT', foto: `/static/teste.jpg` },
        { tipo: 'MSG', mt: `E me conta, o que você gosta de fazer nas férias?` },
        quickbuttons1,
        { tipo: 'MSG', mt: `Ótimo! Obrigado por participar. Tchau!` },
    ],

    //FLUXOS ALTERNATIVOS
    fluxoDepois: [
        { tipo: 'MSG', mt: `Saquei... Tais com preguiça agora.` },
        { tipo: 'MSG', mt: `Ok! Quando você quiser voltar pro fluxo, é só avisar clicar em CONTINUAR.` },
        menuContinuar
    ]

}


